.sub-header {
  padding: 25px 0;
  background-size: 55px;
  margin-top: 4px;
  background-color: #fff;
  position: relative; }
  .sub-header .container .BackButton {
    position: absolute;
    top: -20px; }
  .sub-header .container h1 {
    margin: 0; }
  .sub-header .action-line {
    border-top: 1px solid var(--primary-blue);
    z-index: 0; }
    .sub-header .action-line:after {
      content: "";
      width: 11px;
      height: 11px;
      background: var(--primary-blue);
      border-radius: 5px;
      position: absolute;
      top: -6px;
      right: 0;
      z-index: 1; }
