.input-group .has-error .ant-form-explain, .input-group .has-error .ant-form-split {
  font-size: 11px;
  margin-top: 0; }

.input-group .super input {
  background-color: var(--primary-grey-05);
  color: var(--primary-grey-80);
  border: none; }

.input-group .primary input {
  background-color: #ffffff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.5);
  border: none; }

.input-group input.ant-input-lg {
  font-size: 14px; }

.input-group .ant-btn-lg {
  height: 48px; }
