.multi-select__input {
  min-width: 200px; }

.multi-select .ant-select-selection__choice {
  display: none; }

.multi-select .multi-select__input:not(.ant-select-focused) .ant-select-selection__placeholder {
  display: block !important; }

.multi-select .ant-select-selection--multiple {
  border: none;
  box-shadow: none; }

.multi-select .ant-select-selection__rendered {
  display: flex;
  margin: 0;
  line-height: 36px;
  width: 100%;
  position: absolute;
  z-index: 1; }

.multi-select .ant-select-selection__placeholder {
  margin-left: 11px;
  margin-right: 11px;
  font-size: 12px;
  color: #aba9a8; }

.multi-select .ant-select-selection-selected-value {
  margin-left: 11px; }

.multi-select .ant-select-search__field__wrap {
  margin-left: 11px; }

.multi-select__dropdown {
  display: flex;
  background: #FFF;
  margin-bottom: 16px;
  cursor: pointer; }
  .multi-select__dropdown.no-search .ant-select-selection__rendered {
    cursor: pointer; }
  .multi-select__dropdown.no-search .ant-select-search {
    opacity: 0; }
  .multi-select__dropdown.no-search .multi-select__input .ant-select-selection__placeholder {
    display: block !important; }

.multi-select__input {
  min-width: 200px;
  border: 1px solid #d9d9d9; }

.multi-select__icon {
  transform: translate(-30px);
  width: 0px;
  height: 36px;
  display: flex;
  align-items: center; }
  .multi-select__icon .material-icons {
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    -o-transition: transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s; }
    .multi-select__icon .material-icons.open {
      transform: rotate(-180deg); }
